// common wrapper
.tablewrap
	position: relative
	margin-bottom: 55px

// filters
.filters
	position: relative
	padding: 17px 10px 10px 20px
	margin: 0 -4px
	z-index: 3
	+clr
	&__ttl
		float: left
		margin-top: 5px
		font-size: 17px
		color: #282828
	&__in
		float: right
		font-size: 0

.toggle-btn
	display: inline-block
	width: 33px
	height: 33px
	margin: 0 4px
	border: 1px solid #cfd5e0
	border-radius: 4px
	vertical-align: middle
	cursor: pointer
	+vertical
	&.is-active
		background: #828c92
		border-color: #828c92
		i
			color: #fff
	i
		color: #6c757a
		vertical-align: middle
	.icon-zoom
		font-size: 15px
	.icon-menu
		font-size: 12px

.tabs
	display: inline-block
	margin: 0 4px
	font-size: 0
	vertical-align: middle
	border: 1px solid #cfd5e0
	border-radius: 4px
	&__el
		display: inline-block
		padding: 7.5px 16px
		font-size: 13px
		color: #6c757a
		vertical-align: middle
		border-right: 1px solid #cfd5e0
		cursor: pointer
		&:last-child
			border-right: none
		&.is-active
			background: #828c92
			color: #FFF

// table
.table
	max-width: 100%
	margin-bottom: 45px
	font-size: 13px
	color: #6c757a
	table-layout: fixed
	background: #FFF
	&.is-top-aligned
		td
			padding: 10.5px 8.5px
			vertical-align: top
		.table__drop
			padding-top: 5px
	&.s-bottom-margin
		margin-bottom: 5px
	.table__head
		background: #f7f8f9
		text-align: left
		border-top: 1px solid #efefef
		border-bottom: 1px solid #efefef
		th
			position: relative
			height: 38px
			text-align: left
			padding: 0 10px
			font-size: 13px
			font-weight: normal
			border-right: 1px solid #dce0e7
			cursor: pointer
			vertical-align: middle
			+opensans-sb
			&:before
				display: none
				content: ""
				position: absolute
				top: 50%
				right: 10px
				margin-top: -2px
				+arr(7, 4, #6c757a, b)
			&:hover
				background: rgba(0, 0, 0, 0.05)
			&.is-active
				background: rgba(0, 0, 0, 0.05)
				&:before
					display: block
			&:first-child
				padding-left: 17px
			&:last-child
				border-right: 0
			&.table__drop
				background: none
				&:before
					display: none
			&.table__clip
				text-align: center
			&.table__clip.is-active
				padding-right: 17px
	&__clip
		width: 5%
		text-align: center
		.icon.icon-clip
			font-size: 14px
			vertical-align: -1px
	&__drop
		width: 50px
		padding: 5px 8.5px
		text-align: center
	&__desc
		position: relative
		font-size: 0
		margin-bottom: 6px
		text-transform: capitalize
		&:only-child
			margin: 0
		.svg-ico, .icon
			position: absolute
			top: 50%
			margin-top: -9px
		span
			display: inline-block
			padding-left: 24px
			font-size: 13px
			&:only-child
				padding-left: 0
	&__name
		color: #282828
		+opensans-sb
		&.is-measured
			white-space: nowrap
		span
			display: inline-block
	&__el
		+rmono
		span
			display: inline-block
			vertical-align: middle
	&__more
		display: inline-block
		font-size: 13px
		vertical-align: top
	&__info
		color: #f76300
	tr
		&:first-child
			&:hover
				background: none
		&:hover
			background: darken(#fff, 5%)
		&.is-active
			.table__desc
				color: #097bf2
				+opensans-sb
				~ *:not(.table__info)
					color: #282828
		&.is-pending
			.table__desc
				~ *:not(.table__info)
					color: #96a0a6
			.table__name
				color: #6c757a
				+opensans
		&.table__head
			&:hover
				background: #f7f8f9
	th
		padding: 0
		font-size: 0
		> *
			max-width: 100%
			overflow: hidden
			text-overflow: ellipsis
			white-space: nowrap
		&.txt-right
			text-align: right
			&:before
				right: auto
				left: 10px
	td
		padding: 6px 8.5px
		font-size: 0
		border-top: 1px solid #efefef
		border-bottom: 1px solid #efefef
		vertical-align: middle
		> *:not(.dropdown):not(.table__more)
			max-width: 100%
			font-size: 13px
			overflow: hidden
			text-overflow: ellipsis
			white-space: nowrap
		.icon.icon-clip
			font-size: 14px
		&:first-child
			padding-left: 17px
	.dropdown
		vertical-align: middle
	.status
		padding-left: 1px
		span
			display: inline-block
			max-width: 90%
			overflow: hidden
			text-overflow: ellipsis
			white-space: nowrap
	.svg-ico
		display: inline-block
		width: 16px
		height: 16px
		background-size: 16px 16px
		vertical-align: middle
	.icon
		vertical-align: middle
	.icon-pending
		vertical-align: -2px
	.icon-customer2,
	.icon-corp
		font-size: 17px
	.txt-right
		text-align: right

// sidebar
.sidebar
	position: absolute
	top: 0
	left: 0
	bottom: 0
	width: 0
	padding-bottom: 13px
	background-color: #f7f8f9
	overflow: hidden
	z-index: 2
	transition: width 0.3s ease
	border: 1px solid transparent
	+tr(0.3s ease)
	&.is-active
		width: 240px
		border-color: #dce0e7
		border-width: 1px 1px 1px 0
	&__in
		width: 240px
		height: 100%
	&__content
		height: calc(100% - 38px)
	&__ttl
		position: relative
		max-width: 100%
		height: 38px
		padding: 0 10px
		color: #282828
		overflow: hidden
		white-space: nowrap
		text-overflow: ellipsis
		line-height: 38px
		+opensans-sb
		i
			position: absolute
			top: 50%
			right: 10px
			margin-top: -4.5px
			cursor: pointer
			+arr(6, 9, #6c757a, l)
	&__option
		&.is-active
			p
				background: #dce0e7
		p, a
			display: block
			padding: 3px 20px
			font-size: 13px
			color: #6c757a
			white-space: nowrap
			&:hover
				background: rgba(#dce0e7, 0.5)
			.icon
				margin-right: 5px
				font-size: 11px
				color: #899bac
				vertical-align: middle
			.svg-ico
				width: 10px
				height: 10px
				margin-right: 5px
				background-size: 10px 10px
				vertical-align: middle
		p
			cursor: pointer
		span
			display: inline-block
			max-width: 100%
			overflow: hidden
			vertical-align: middle
			text-overflow: ellipsis
			white-space: nowrap
	&__sublist
		display: none
		padding: 5px 20px
	&__bl
		padding: 10px 20px
	&__link
		display: block
		margin-bottom: 2px
		font-size: 12px
		color: #6c757a
		+opensans-sb
		&:hover
			text-decoration: underline

// scrolling part of table
.scrollbar
	//position: relative
	margin-right: 50px
	+tr(0.3s ease)
	&:before
		content: ""
		position: absolute
		left: 0
		bottom: 0
		width: 240px
		height: 13px
		background: #f7f8f9
		border-top: 1px solid #dce0e7
		border-right: 1px solid #dce0e7
		border-bottom: 1px solid #dce0e7
		opacity: 0
	&:after
		content: ""
		position: absolute
		right: 0
		bottom: 0
		width: 50px
		height: 13px
		background: #f7f8f9
		border-left: 1px solid #dce0e7
		border-bottom: 1px solid #dce0e7
	&.is-sidebar
		margin-left: 240px
	.table
		min-width: 1000px
		margin-bottom: 0
		background: #FFF
		td
			padding: 9px 8.5px 6px
			height: 38px
		td > span
			display: inline-block
	.mCSB_horizontal.mCSB_inside > .mCSB_container
		margin-bottom: 12px
	.mCustomScrollBox
	.mCSB_draggerContainer
		margin: 0 28px 0 0 !important
		border: 1px solid #dce0e7
		border-left: none
		overflow: hidden
		z-index: 0
	.mCSB_scrollTools
		opacity: 1
		.mCSB_dragger
			z-index: 0
	.mCSB_scrollTools.mCSB_scrollTools_horizontal
		height: 13px
	.mCSB_scrollTools.mCSB_scrollTools_horizontal .mCSB_draggerRail
		height: 100%
		margin: 0
		border-radius: 0
		background: #f7f8f9
	.mCSB_scrollTools.mCSB_scrollTools_horizontal .mCSB_dragger .mCSB_dragger_bar
		height: 7px
		margin: 2px auto
		background: #cacdd3
		border-radius: 0
	.mCSB_scrollTools.mCSB_scrollTools_horizontal .mCSB_buttonRight,
	.mCSB_scrollTools.mCSB_scrollTools_horizontal .mCSB_buttonLeft
		width: 14px
		background: #f7f8f9
		border: 1px solid #dce0e7
		opacity: 1
		&:before
			content: ""
			position: absolute
			top: 50%
			left: 50%
			margin: -2.5px 0 0 -2px
	.mCSB_scrollTools.mCSB_scrollTools_horizontal .mCSB_buttonRight
		border-right: none
		&:before
			+arr(4, 5, #cacdd3, r)
	.mCSB_scrollTools.mCSB_scrollTools_horizontal .mCSB_buttonLeft
		right: 14px
		left: auto
		border-left: none
		&:before
			+arr(4, 5, #cacdd3, l)

// sidedrop
.sidedrop
	position: absolute
	top: 0
	right: 0
	width: 50px
	text-align: center
	z-index: 2
	.table
		margin-bottom: 0
		border-left: 1px solid #dce0e7
		background: #FFF
		td
			padding: 0
			vertical-align: middle
			border-bottom: 1px solid #efefef
			&:first-child
				width: auto
				padding-left: 0
	.table__head
		width: auto
		padding-left: 0
		td
			height: 38px
	.dropdown
		vertical-align: middle

.show-more
	display: inline-block
	padding: 5px 0
	color: #097bf2
	vertical-align: middle


// fake header
.fake-header
	position: fixed
	top: 0
	left: 0
	right: 0
	padding: 0 20px
	opacity: 0
	visibility: hidden
	z-index: 1
	&.is-visible
		opacity: 1
		visibility: visible
	&.is-sidebar
		padding-left: 260px
	.l-table
		max-width: 1330px
		margin: 0 auto
		overflow: hidden
	.table
		position: relative
		min-width: 1000px
		margin-bottom: 0